import { auth, provider, storage } from "../firebase";
import db from "../firebase";
import { SET_INITIAL_POST, SET_LOADING_STATUS, SET_USER } from "./type";

export const setUser = (payload) => ({
  type: SET_USER,
  user: payload,
});

export const setLoading = (status) => ({
  type: SET_LOADING_STATUS,
  status: status,
});

export const setInitialPost = (payload) => {
  console.log(payload);
  return {
    type: SET_INITIAL_POST,
    payload: payload,
  };
};

export const signInAPI = () => (dispatch) => {
  auth
    .signInWithPopup(provider)
    .then((payload) => {
      dispatch(setUser(payload.user));
    })
    .catch((err) => alert(err.message));
};

export const getUserAuth = () => (dispatch) => {
  auth.onAuthStateChanged(async (user) => {
    if (user) {
      console.log(user);
      dispatch(setUser(user));
    }
  });
};

export const signOutAPI = () => (dispatch) => {
  auth
    .signOut()
    .then(() => {
      dispatch(setUser(null));
    })
    .catch((err) => {
      console.log(err.message);
    });
};

export const postArticleAPI = (payload) => (dispatch) => {
  dispatch(setLoading(true));
  if (payload.image != "") {
    const upload = storage
      .ref(`images/${payload.image.name}`)
      .put(payload.image);
    upload.on(
      "state_change",
      (snapshot) => {
        const progress =
          (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        console.log(`Progress: ${progress}%`);

        if (snapshot.state === "RUNNING") {
          console.log()(`Progress: ${progress}%`);
        }
      },
      (err) => {
        console.log(err.code);
      },
      async () => {
        const downloadURL = await upload.snapshot.ref.getDownloadURL();
        db.collection("articles").add({
          actor: {
            description: payload.user.email,
            title: payload.user.displayName,
            date: payload.timestamp,
            image: payload.user.photoURL,
          },
          video: payload.video,
          sharedImg: downloadURL,
          comments: 0,
          description: payload.description,
        });
        dispatch(setLoading(false));
      }
    );
  } else if (payload.video) {
    console.log("Video hahaha");
    db.collection("articles").add({
      actor: {
        description: payload.user.email,
        title: payload.user.displayName,
        date: payload.timestamp,
        image: payload.user.photoURL,
      },
      video: payload.video,
      sharedImg: "",
      comments: 0,
      description: payload.description,
    });
    dispatch(setLoading(false));
  }
};

export const getArticlesAPI = () => (dispatch) => {
  let payload;

  db.collection("articles")
    .orderBy("actor.date", "desc")
    .onSnapshot((snapshot) => {
      payload = snapshot.docs.map((doc) => doc.data());
      console.log(payload);
      dispatch(setInitialPost(payload));
    });
};
