import React from "react";
import styled from "styled-components";
import { connect } from "react-redux";
import { useState, useEffect } from "react";
import PostModal from "./PostModal";
import { getArticlesAPI } from "../actions";
import ReactPlayer from "react-player";

function Main(props) {
  const [showModal, setShowModal] = useState(false);

  useEffect(() => {
    props.getArticles();
  }, []);

  const handleClick = (e) => {
    e.preventDefault();
    console.log(e.target, e.currentTarget);
    if (e.target !== e.currentTarget) {
      return;
    }

    setShowModal(!showModal);
  };

  return (
    <>
      {props.articles.length === 0 ? (
        <p>There are no aritcles!!!!!</p>
      ) : (
        <Container>
          <ShareBox>
            {}
            <div>
              {props.user && props.user.photoURL ? (
                <img src={props.user.photoURL} />
              ) : (
                <img src="/images/user.svg" />
              )}
              <button
                onClick={handleClick}
                disabled={props.loading ? true : false}
              >
                Start a post
              </button>
            </div>

            <div>
              <button>
                <img src="images/photo-icon.svg" />
                <span>Photo</span>
              </button>
              <button>
                <img src="/images/video-icon.svg" />
                <span>Video</span>
              </button>
              <button>
                <img src="/images/event-icon.svg" />
                <span>Event</span>
              </button>
              <button>
                <img src="/images/write-icon.svg" />
                <span>Write article</span>
              </button>
            </div>
          </ShareBox>
          <Content>
            {props.loading && <img src={"/images/spin-loader.svg"} />}
            {props.articles.length > 0 &&
              props.articles.map((article, i) => {
                return (
                  <Article key={i}>
                    <ShareActor>
                      <a>
                        <img src={article.actor.image} />
                        <div>
                          <span>{article.actor.title}</span>
                          <span>{article.actor.description}</span>
                          <span>
                            {article.actor.date.toDate().toLocaleDateString()}
                          </span>
                        </div>
                      </a>
                      <button>
                        <img src="/images/ellipsis.svg" />
                      </button>
                    </ShareActor>
                    <Description>{article.description}</Description>
                    <ShareImg>
                      <a>
                        {article.sharedImg ? (
                          <img src={article.sharedImg} />
                        ) : (
                          <ReactPlayer width={"100%"} url={article.video} />
                        )}
                      </a>
                    </ShareImg>
                    <SocialCounts>
                      <li>
                        <button>
                          <img src="https://static-exp1.licdn.com/sc/h/d310t2g24pvdy4pt1jkedo4yb" />
                          <img src="https://static-exp1.licdn.com/sc/h/5thsbmikm6a8uov24ygwd914f" />
                          <span>75</span>
                        </button>
                      </li>
                      <li>
                        <a>{article.comment} comments</a>
                      </li>
                    </SocialCounts>
                    <SocialActions>
                      <button>
                        <img src="/images/like-icon.svg" />
                        <span>Like</span>
                      </button>
                      <button>
                        <img src="/images/comments-icon.svg" />
                        <span>Comments</span>
                      </button>
                      <button>
                        <img src="/images/share-icon.svg" />
                        <span>Share</span>
                      </button>
                      <button>
                        <img src="/images/send-icon.svg" />
                        <span>Send</span>
                      </button>
                    </SocialActions>
                  </Article>
                );
              })}
          </Content>

          {showModal && (
            <PostModal
              showModal={showModal}
              handleClick={handleClick}
              setShowModal={setShowModal}
            />
          )}
        </Container>
      )}
    </>
  );
}

const mapStateToProps = (state) => {
  return {
    user: state.userState.user,
    loading: state.articleState.loading,
    articles: state.articleState.articles,
  };
};

const mapDispatchToProps = {
  getArticles: getArticlesAPI,
};

export default connect(mapStateToProps, mapDispatchToProps)(Main);

const Container = styled.div`
  grid-area: main;
`;

const CommonCard = styled.div`
  text-align: center;
  overflow: hidden;
  margin-bottom: 8px;
  background-color: #fff;
  border-radius: 5px;
  position: relative;
  border: none;
  box-shadow: 0 0 0 1px rgb(0 0 0 / 15%), 0 0 0 rgb(0 0 0 / 20%);
`;

