import React from "react";
import styled from "styled-components";
import { useState, useEffect, useRef } from "react";
import { connect } from "react-redux";
import ReactPlayer from "react-player";
import firebase from "firebase";
import { postArticleAPI } from "../actions";

function PostModal(props) {
  const [editorText, setEditorText] = useState("");
  const [shareImg, setShareImg] = useState("");
  const [videoLink, setVideoLink] = useState("");
  const [assetArea, setAssetArea] = useState("");
  const element = useRef();

  const reset = (e) => {
    setEditorText("");
    setShareImg("");
    setVideoLink("");
    setAssetArea("");
    props.handleClick(e);
  };

  const handleChange = (e) => {
    const image = e.target.files[0];
    console.log(e.target.files);

    if (image === "" || image === undefined) {
      alert(`not an image, the file is a ${typeof image}`);
      return;
    }

    setShareImg(image);
  };

  const switchAssetArea = (area) => {
    setShareImg("");
    setVideoLink("");
    setAssetArea(area);
  };

  const postAritcle = (e) => {
    if (e.target !== e.currentTarget) {
      return;
    }

    const payload = {
      image: shareImg,
      video: videoLink,
      user: props.user,
      description: editorText,
      timestamp: firebase.firestore.Timestamp.now(),
    };
    props.postArticleAPI(payload);
    reset(e);
  };

  const modalMode = (e) => {
    if (e.key === "Escape") {
      props.setShowModal(false);
    }
  };

  useEffect(() => {
    element.current.addEventListener("keydown", modalMode);
  }, []);

  return (
    <>
      (
      <Container ref={element}>
        <Content>
          <Header>
            <h2>Create a post</h2>
            <button onClick={reset}>
              <img src="/images/close-icon.svg" />
            </button>
          </Header>
          <ShareContent>
            <UserInfo>
              {props.user && props.user.photoURL ? (
                <img src={props.user.photoURL} />
              ) : (
                <img src="/images/user.svg" />
              )}
              <span>{props.user ? props.user.displayName : "Name"}</span>
            </UserInfo>
            <Editor>
              <textarea
                value={editorText}
                onChange={(e) => setEditorText(e.target.value)}
                placeholder={`What do you want to talk about? \n\n\n\n (You can choose image or video by clicking below button)`}
                autoFocus={true}
              />

              {assetArea === "image" ? (
                <UploadImg>
                  <input
                    type="file"
                    onChange={handleChange}
                    accept="image/gif, image/jpg, image/png"
                    name="image"
                    id="file"
                    style={{ display: "none " }}
                  />
                  <p>
                    <label htmlFor="file">Select a image to share</label>
                  </p>
                  {shareImg && <img src={URL.createObjectURL(shareImg)} />}
                </UploadImg>
              ) : (
                assetArea === "media" && (
                  <UploadVideo>
                    <input
                      type="text"
                      placeholder="Please input a video link"
                      value={videoLink}
                      onChange={(e) => setVideoLink(e.target.value)}
                    />
                    {videoLink && (
                      <ReactPlayer width={"100%"} url={videoLink} />
                    )}
                  </UploadVideo>
                )
              )}
            </Editor>
          </ShareContent>
          <ShareCreation>
            <AttachAssets>
              <AssetButton
                onClick={(e) => {
                  e.preventDefault();
                  switchAssetArea("image");
                }}
              >
                <img src="/images/share-img.svg" />
              </AssetButton>{" "}
              <AssetButton
                onClick={(e) => {
                  e.preventDefault();
                  switchAssetArea("media");
                }}
              >
                <img src="/images/share-video.svg" />
              </AssetButton>
            </AttachAssets>
            <ShareComment>
              <AssetButton>
                <img src="/images/share-cmt.svg" />
                Anyone
              </AssetButton>
            </ShareComment>
            <PostButton
              disabled={!editorText ? true : false}
              onClick={postAritcle}
            >
              Post
            </PostButton>
          </ShareCreation>
        </Content>
      </Container>
      )
    </>
  );
}

const mapStateToProps = (state) => {
  return { user: state.userState.user };
};

const mapDispatchToProps = {
  postArticleAPI,
};

export default connect(mapStateToProps, mapDispatchToProps)(PostModal);

